canvas {
    display: block; /* fix necessary to remove space at bottom of canvas */
}

.techdiv {
  display: flow-root;
}

.img-container{
  position:relative;
  display:inline-block;
}
.img-container img{
  display:block;
 }
.img-container .overlay{
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background:rgba(0,0,0,0.9);
  opacity:0;
  transition:opacity 200ms ease-in-out;
}
.img-container:hover .overlay{
  opacity:1;
}
.overlay span{
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  color:#fff;
}

.tech {
  background-color: black;
  color: white;
  border: 1px solid grey;
  padding: 5px;
  float: left;
  margin: 5px;
}

.resetBtn {
  position: absolute;
  left: 50%;
  top: 3%;
  transform: translateX(-50%) translateY(-50%);
  width: 30px;
  height: 30px;
}

.pulsating-circle {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 30px;
  height: 30px;
}
.pulsating-circle:before {
  content: '';
  position: relative;
  display: block;
  width: 300%;
  height: 300%;
  box-sizing: border-box;
  margin-left: -100%;
  margin-top: -100%;
  border-radius: 45px;
  background-color: grey;
  animation: pulse-ring 1.25s cubic-bezier(0.215, 0.61, 0.355, 1) infinite;
}
.pulsating-circle:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 0 8px rgba(0, 0, 0, .3);
  animation: pulse-dot 1.25s cubic-bezier(0.455, 0.03, 0.515, 0.955) -0.4s infinite;
}
@keyframes pulse-ring {
  0% {
    transform: scale(0.33);
  }
  80%, 100% {
    opacity: 0;
  }
}
@keyframes pulse-dot {
  0% {
    transform: scale(0.8);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.8);
  }
}

.goBackButton {
  background-color: rgba(0,0,0,0.9);
  color: white;
  height: 35px;
  max-width: 800px;
  margin: auto;
}

.goBackText {
  padding-top: 5px;
}

.goBackButton:hover {
  cursor: pointer;
  text-decoration: underline;
}
